





















































import {Component, Vue} from "vue-property-decorator";
import HelloWorld from "./components/HelloWorld.vue";
// @ts-ignore
import VueQrcode from "@chenfengyuan/vue-qrcode";

import XLSX from "xlsx";
import createReport from "docx-templates";
import JSZip from "jszip";
import {saveAs} from "file-saver";

@Component({
  components: {
    HelloWorld,
    VueQrcode,
  },
})
export default class App extends Vue {
  word: File | null = null;
  excel: File | null = null;
  progress: number | null = null;

  async handleUpload() {
    if (!this.word || !this.excel) return;
    if (this.progress !== null) return;
    this.progress = 0;

    const workbook = XLSX.read(await this.excel.arrayBuffer(), {
      type: "array",
    });

    const sheet = workbook.Sheets[workbook.SheetNames[0]];
    let data: any[] = XLSX.utils.sheet_to_json(sheet, {
      range: "A4:ZZ9999",
      defval: "",
    });
    console.log(data);
    data = data.filter((d) =>
        [
          "轻度1",
          "轻度2",
          "轻度3",
          "轻度4",
          "中度1",
          "中度2",
          "中度3",
          "中度4",
        ].includes(d.模版)
    );

    console.log(data);

    const template = await this.word.arrayBuffer();

    const zip = new JSZip();

    for (const [index, row] of data.entries()) {
      this.progress = index / data.length;
      const report = await createReport({
        // @ts-ignore
        template,
        cmdDelimiter: ["{", "}"],
        data: row,
      });

      zip.file(`心理评测报告_${row.姓名}.docx`, report);
    }

    this.progress = 1;
    const content = await zip.generateAsync({type: "blob"});

    saveAs(content, "report.zip");

    this.progress = null;
  }

  selectWord(file: File) {
    this.word = file;
    return false;
  }

  selectExcel(file: File) {
    this.excel = file;
    return false;
  }
}
